.page-basket-step2 {
	margin-top: 25px;

	.grid__r317 {
		color: #000;
		font-size: 14px;
		font-style: normal;
		line-height: lh(14,26);
		display: flex;
		flex-direction: column;
		gap: 25px;
		&.show {
			.aside-info--hide {
				display: block;
			}
		}
	}

	p {
		color: #333;
		font-weight: 400;
		line-height: lh(14,30);
	}

	.form-field {
		&__caption {
			margin-top: 25px;
			font-weight: 600;
		}
		.select {
			margin-bottom: 10px;
		}
		textarea {
			color: #666;
			font-weight: 400;
			font-size: 13px;
			min-height: 96px;
		}

	}

	.aside-info {



		&:nth-child(1) {
			.form-field {
				&__caption {
					margin-top: 0;
				}
			}
		}

		&__total {
			color: #333;
			font-size: 20px;
			font-style: normal;
			font-weight: 500;
			line-height: 30px; /* 150% */
			min-height: 47px;
			display: flex;
			align-items: flex-end;
			justify-content: space-between;
			border-top: 1px solid  #D0D0D0;
			margin-top: 12px;
			span {
				color: #000;
				font-size: 24px;
				font-weight: 700;
				line-height: lh(24,30);
			}

		}

		&--flex {
			display: flex;
			justify-content: space-between;
			border-radius: 15px;
			border: 1px solid #E2CE91;
			background: #F9DF92;

			p {
				font-size: 14px;
				font-style: normal;
				font-weight: 500;
				line-height: 18px;
				max-width: 150px;
				color: #000;
			}

			.aside-info__show {
				color: #675727;
				font-size: 12px;
				font-weight: 400;
				line-height: lh(12, 24);
				cursor: pointer;
				text-decoration: underline dotted #675727;
			}
		}

		&--hide {
			display: none;
		}


	}



	.btn {
		border-radius: 10px;
		background: #FFB444;
		font-size: 16px;
		font-weight: 500;
		display: flex;
		align-items: center;
		justify-content: center;
		height: 65px;
		margin-bottom: 80px;

	}





}
