.table-custom {
	margin-bottom: 80px;
	font-size: 14px;
	font-style: normal;
	font-weight: 400;
	line-height: 26px;
	&__title {
		display: grid;
		grid-template-columns: 22px 1fr 152px 92px 60px;
		color: #999;
		border-bottom: 2px solid #C8C8C8;
		min-height: 33px;
	}

	&__item {
		display: grid;
		grid-template-columns: 22px 1fr 152px 92px 60px;
		color: #333;
		min-height: 77px;
		align-items: center;
		position: relative;
		border-bottom: 1px solid #EAEAEA;
	}
}
